html {
  min-height: 100%;
  text-size-adjust: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  scroll-behavior: smooth;
}

article,
aside,
figcaption,
figure,
footer,
header,
hgroup,
main,
nav,
section {
  display: block;
}

section {
  width: 100%;
}

body {
  position: relative;
  max-width: 100%;
  height: 100vh;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  color: #ffffff;
  font-family: "Lexend", arial, sans-serif;
  text-align: left;
  background-color: #110a1d;
  background-image: url("../img/pictures/blur-bg-2.svg");
  background-repeat: no-repeat;
  background-position: right bottom;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

p {
  margin: 0;
}

ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}

button:focus {
  outline: 0;
}

table {
  border-collapse: collapse;
}

img {
  border-style: none;
}

img,
svg {
  vertical-align: middle;
}

svg {
  overflow: hidden;
}

a {
  color: #ffffff;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

fieldset {
  padding: 0;
  border: none;
}

button {
  margin: unset;
  padding: unset;
  border: unset;
  border-radius: unset;
  background-color: unset;
}

[tabindex="-1"]:focus:not(:focus-visible) {
  outline: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  overflow: hidden;
  clip: rect(0 0 0 0);
}

.main-link {
  transition: all 0.3s ease;
}

.main-link:hover {
  color: #478bf9;
  text-decoration: underline;
}

.main-button {
  min-width: 97px;
  padding: 14px;
  border-radius: 24px;
  color: #ffffff;
  text-align: center;
  text-transform: uppercase;
  background-color: #478bf9;
  cursor: pointer;
  transition: all 0.3s ease;
}

.main-button:hover {
  color: #478bf9;
  background-color: #ffffff;
}

.main-heading {
  font-weight: 400;
  font-size: 24px;
  line-height: 28px;
}

.secondary-button {
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-width: 75px;
  padding: 14px;
  border: 2px solid #9dadf2;
  border-radius: 24px;
  color: #ffffff;
  text-align: center;
  text-transform: uppercase;
  background-color: transparent;
  cursor: pointer;
  transition: all 0.3s ease;
}

.secondary-button:hover {
  border-color: #478bf9;
  background-color: #478bf9;
}

.secondary-button img {
  width: 15px;
  height: 15px;
}

.blog-date-text {
  color: rgba(255, 255, 255, 0.6);
  font-family: "Futura PT", sans-serif;
  font-weight: 450;
  font-size: 14px;
  line-height: 16px;
}

.section-container {
  max-width: 1640px;
  margin: 0 auto;
  padding-right: 20px;
  padding-left: 20px;
}

.link-list ul {
  display: flex;
  gap: 40px;
  align-items: center;
  justify-content: space-between;
}

.socials {
  display: flex;
  flex-direction: column;
  gap: 14px;
  align-items: center;
  justify-content: space-between;
}

.socials__list {
  display: flex;
  gap: 8px;
  align-items: center;
}

.socials__list li {
  position: relative;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  cursor: pointer;
}

.socials__list svg {
  width: 100%;
  height: 100%;
  transition: all 0.3s ease;
}

.socials__list svg path {
  transition: all 0.3s ease;
}

.socials__list li:hover path {
  fill: #ffffff;
}

.socials h3 {
  margin-bottom: 14px;
  margin-left: auto;
}

.socials img {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 16px;
  height: 16px;
  transform: translate(-50%, -50%);
}

.hero-text-content__title {
  margin-bottom: 24px;
  font-weight: 500;
  font-size: 96px;
  line-height: 96px;
  text-transform: uppercase;
}

.hero-text-content__title span {
  color: #478bf9;
}

.hero-text-content__text {
  font-weight: 500;
  font-size: 32px;
  line-height: 32px;
  margin-bottom: 48px;
  color: rgba(255, 255, 255, 0.6);
}

.hero-text-content .main-button {
  min-width: 393px;
  padding: 20px 72px;
}

.hero-blog__title {
  margin-bottom: 24px;
  text-align: center;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 40px;
  line-height: 40px;
}

.hero-blog__wrapper {
  height: 595px;
  padding-top: 20px;
  padding-right: 45px;
  padding-bottom: 20px;
  padding-left: 10px;
  border: 3px solid #478bf9;
  border-radius: 100px;
  overflow: hidden;
  background-color: rgba(255, 255, 255, 0.12);
}

.hero-blog__container {
  position: relative;
  height: 100%;
  padding: 24px;
  padding-top: 0;
  padding-right: 26px;
  padding-bottom: 0;
  overflow: hidden;
  overflow-y: scroll;
}

@-moz-document url-prefix()  {
  .hero-blog__container {
    scrollbar-color: #478bf9 rgba(255, 255, 255, 0.12);
    scrollbar-width: thin;
  }
}

.hero-blog__container::-webkit-scrollbar-track {
  border-radius: 50px;
  background-color: rgba(255, 255, 255, 0.12);
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

.hero-blog__container::-webkit-scrollbar {
  width: 12px;
  height: 12px;
  border-radius: 50px;
  background-color: rgba(255, 255, 255, 0.12);
}

.hero-blog__container::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: #478bf9;
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

.hero-blog__list {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.hero-blog__list li {
  padding: 16px 54px;
  border-radius: 196px;
  text-align: left;
  background-color: #1e1231;
  cursor: pointer;
  transition: all 0.3 ease;
}

.hero-blog__list li:hover {
  background-color: #478bf9;
}

.hero-blog__list a {
  text-decoration: none;
}

.hero-blog .main-heading {
  margin-bottom: 14px;
}

.hero-animated-lines {
  width: 30%;
  height: 100%;
  overflow: hidden;
}

.hero-animated-lines__wrapper {
  display: flex;
  gap: 16px;
  height: 1000px;
  overflow: hidden;
}

@keyframes topLineAnimation {
  from {
    transform: translateY(0);
  }

  to {
    transform: translateY(-100%);
  }
}

@keyframes downLineAnimation {
  from {
    transform: translateY(-100%);
  }

  to {
    transform: translateY(0);
  }
}

@keyframes rightLineAnimation {
  from {
    transform: translateX(-70%);
  }

  to {
    transform: translateX(0%);
  }
}

@keyframes leftLineAnimation {
  from {
    transform: translateX(0%);
  }

  to {
    transform: translateX(-70%);
  }
}

.hero-animated-lines__line {
  display: inline-block;
  width: 100px;
  height: auto;
}

.hero-animated-lines__wrapper--desktop.animate .hero-animated-lines__line:nth-child(1) {
  animation: 10s downLineAnimation infinite linear;
}

.hero-animated-lines__wrapper--desktop.animate .hero-animated-lines__line:nth-child(2) {
  animation: 10s topLineAnimation infinite linear;
}

.hero-animated-lines__wrapper--desktop.animate .hero-animated-lines__line:nth-child(3) {
  animation: 10s downLineAnimation infinite linear;
}

.header {
  position: relative;
  max-width: 100%;
  margin-top: 16px;
}

.header__wrapper {
  padding: 24px 48px;
  border: 4px solid #478bf9;
  border-radius: 64px;
  background-color: #110a1d;
}

.header__grid {
  display: flex;
  align-items: center;
  transition: all 0.3s ease;
}

.header .link-list {
  align-self: center;
}

.header .link-list ul {
  gap: 32px;
  margin-right: 20px;
  margin-left: 20px;
}

.header .secondary-link span {
  margin: 0 auto;
}

.header nav {
  margin-left: 30%;
}

.header__user-controls {
  margin-left: auto;
}

.header__user-controls ul {
  display: flex;
  gap: 14px;
  align-items: center;
  justify-content: space-between;
}

.header__user-controls li {
  position: relative;
}

.header__user-controls .secondary-button {
  gap: 5px;
}

.header__language-button {
  transition: all 0.3s ease;
}

.header__language-button.active {
  min-width: 95px;
}

.header__language-button--mobile {
  display: none;
  gap: 5px;
  width: 36px;
  min-width: 36px;
  height: 36px;
  border-radius: 50%;
}

.header__language-button--mobile span {
  display: none;
}

.header__language-button--mobile.active {
  border-radius: 24px;
}

.header__burger-button {
  display: none;
  width: 28px;
  height: 28px;
  padding: 4px;
  cursor: pointer;
}

.header__burger-button img {
  width: 28px;
  height: 28px;
}

.header__close-icon {
  display: none;
}

.header__language-option {
  display: none;
  cursor: pointer;
}

.header__language-option:hover {
  text-decoration: underline;
}

.header__language-option.active {
  display: inline;
}

.footer {
  padding-top: 40px;
  padding-bottom: 24px;
  border-top: 4px solid #478bf9;
  border-radius: 64px;
}

.footer__content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0 auto;
  padding-right: 48px;
  padding-left: 48px;
}

.footer .link-list {
  padding-top: 46px;
}

.hero {
  background-image: url("../img/pictures/blur-bg-1.svg");
  background-repeat: no-repeat;
  background-position: left top;
}

.hero .hero-animated-lines__wrapper--mobile {
  display: none;
}

.hero .hero-animated-lines__wrapper--desktop {
  display: flex;
}

.hero__wrapper {
  display: flex;
  justify-content: space-between;
}

.hero .hero-text-content {
  width: 700px;
  margin-right: 42px;
  padding-top: 296px;
}

.hero .main-button {
  border-radius: 48px;
  font-weight: 600;
  font-size: 24px;
  line-height: 32px;
}

.hero .hero-blog {
  margin-left: 64.2px;
  padding-top: 140px;
}

@media (max-width: 1440px) {
  body {
    background-position: right -340%;
  }

  .hero-animated-lines {
    width: auto;
  }

  .header nav {
    margin-left: 20%;
  }

  .hero {
    padding-bottom: 40px;
  }

  .hero__wrapper {
    flex-direction: column;
    align-items: center;
  }

  .hero .hero-text-content {
    max-width: 100%;
    margin-right: 0;
    margin-bottom: 32px;
    padding-top: 70px;
    text-align: center;
  }

  .hero .main-button {
    min-width: 30%;
  }

  .hero .hero-blog {
    margin-left: 0;
    padding-top: 33px;
    padding-right: 10px;
    padding-left: 10px;
  }
}

@media (max-width: 1280px) {
  .header nav {
    margin-left: 10%;
  }
}

@media (max-width: 1200px) {
  .header nav {
    margin-left: 0;
  }
}

@media (max-width: 1050px) {
  .link-list ul {
    flex-direction: column;
    gap: 2px;
  }

  .socials h3 {
    font-weight: 400;
  }

  .socials {
    flex-direction: column;
    gap: 2px;
  }

  .socials__list li {
    width: 27px;
    height: 27px;
  }

  .socials h3 {
    margin-bottom: 8px;
    font-size: 14px;
    line-height: 20px;
    text-align: left;
  }

  .socials img {
    width: 13.5px;
    height: 13.5px;
  }

  .hero-text-content__title {
    margin-bottom: 12px;
    font-size: 46px;
    line-height: 50px;
    text-align: center;
  }

  .hero-text-content__text {
    margin-bottom: 24px;
    font-size: 20px;
    line-height: 26px;
    text-align: center;
  }

  .hero-blog__title {
    margin-bottom: 18px;
    font-size: 32px;
    line-height: 40px;
  }

  .hero-blog__wrapper {
    padding: 10px;
    padding-right: 20px;
    border: 2px solid #478bf9;
    border-radius: 50px;
  }

  .hero-blog__container {
    padding: 10px;
  }

  .hero-blog__list li {
    padding: 15px 35px;
    border-radius: 40px;
  }

  .hero-blog__list {
    gap: 10px;
  }

  .hero-blog .main-heading {
    margin-bottom: 6px;
  }

  .hero-animated-lines__wrapper--mobile.animate .hero-animated-lines__line {
    display: flex;
    width: 100%;
  }

  .hero-animated-lines__wrapper--mobile.animate .hero-animated-lines__line:nth-child(1) {
    animation: 10s rightLineAnimation infinite linear;
  }

  .hero-animated-lines__wrapper--mobile.animate .hero-animated-lines__line:nth-child(2) {
    animation: 10s leftLineAnimation infinite linear;
  }

  .hero-animated-lines__wrapper--mobile.animate .hero-animated-lines__line:nth-child(3) {
    animation: 10s rightLineAnimation infinite linear;
  }

  .header__wrapper {
    display: flex;
    padding: 10px 14px;
    border: 2px solid #478bf9;
  }

  .header__grid {
    display: none;
  }

  .header__grid.active {
    position: absolute;
    top: 90px;
    right: 0;
    z-index: 100;
    display: block;
    width: 100%;
    height: 100vh;
    padding-bottom: 10px;
    background-color: #110a1d;
  }

  .header .link-list ul {
    flex-direction: column;
    gap: 5px;
    justify-content: flex-start;
  }

  .header .link-list li {
    width: 100%;
    padding: 13px 20px;
    border: 2px solid #478bf9;
    border-radius: 28px;
  }

  .header .link-list .main-link {
    font-size: 18px;
    line-height: 24px;
  }

  .header .secondary-link {
    text-align: center;
  }

  .header nav {
    margin-bottom: 30px;
  }

  .header__user-controls ul {
    flex-direction: column;
    gap: 5px;
    padding-right: 20px;
    padding-left: 20px;
  }

  .header__user-controls li {
    width: 100%;
  }

  .header__user-controls .secondary-button {
    width: 100%;
  }

  .header__user-controls .main-button {
    width: 100%;
  }

  .header__language-button--mobile {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 4px;
    margin-left: auto;
  }

  .header__language-button--desktop {
    display: none;
  }

  .header__burger-button {
    display: block;
  }

  .footer {
    padding-top: 20px;
    padding-bottom: 30px;
    border-top: 2px solid #478bf9;
    border-radius: 50px;
  }

  .footer__content {
    flex-direction: column;
    gap: 20px;
    padding-right: 0;
    padding-left: 0;
  }

  .footer .link-list {
    width: 100%;
    padding-top: 0;
  }

  .footer .link-list li {
    width: 100%;
  }

  .footer .socials {
    align-items: flex-start;
    width: 100%;
  }

  .footer .socials h3 {
    margin-left: 0;
  }

  .hero .hero-animated-lines__wrapper--mobile {
    display: flex;
    flex-direction: column;
    gap: 8.16px;
    width: 100%;
    overflow: hidden;
  }

  .hero .hero-animated-lines__wrapper--desktop {
    display: none;
  }

  .hero .section-container {
    max-width: 100%;
    padding-right: 0;
    padding-left: 0;
  }

  .hero {
    padding-bottom: 119px;
    background-size: 90%;
  }

  .hero .main-button {
    padding-right: 10px;
    padding-left: 10px;
    font-size: 18px;
    line-height: 22px;
  }
}

@media (max-width: 320px) {
  .main-link {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
  }

  .main-button {
    font-size: 16px;
    line-height: 20px;
  }

  .main-heading {
    font-size: 16px;
    line-height: 20px;
  }

  .secondary-button {
    font-size: 16px;
    line-height: 20px;
  }

  .secondary-button img {
    width: 18.7px;
    height: 18.7px;
  }

  .section-container {
    max-width: 300px;
    padding-right: 10px;
    padding-left: 10px;
  }

  .hero-text-content .main-button {
    min-width: 100%;
    padding: 15px 56.5px;
  }

  .hero-animated-lines__wrapper {
    display: none;
  }

  .hero-animated-lines {
    width: 100%;
    height: auto;
  }

  .hero .hero-text-content {
    max-width: 300px;
    margin: 0 auto;
    margin-bottom: 32px;
    padding-top: 70px;
    padding-right: 10px;
    padding-left: 10px;
  }

  .hero .main-button {
    min-width: 100%;
    padding-right: 10px;
    padding-left: 10px;
    font-size: 18px;
    line-height: 22px;
  }

  .hero .hero-blog {
    max-width: 300px;
    margin: 0 auto;
  }

  .hero .hero-animated-lines__wrapper {
    flex-direction: column;
  }
}